.planner-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.planner-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.chat-box {
  max-height: 600px;
  overflow-y: auto;
  background: #f7f7f7;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 15px;
}

.chat-message {
  display: flex;
  margin: 10px 0;
}

.chat-message.user {
  justify-content: flex-end;
}

.chat-message.ai {
  justify-content: flex-start;
}

.chat-bubble {
  max-width: 70%;
  padding: 12px 16px;
  border-radius: 16px;
  line-height: 1.5;
  white-space: pre-wrap;
  background-color: #e1f5fe;
  color: #000;
}

.chat-message.user .chat-bubble {
  background-color: #dcedc8;
  color: #000;
}

.chat-input-form {
  display: flex;
  gap: 10px;
}

.chat-input-form input {
  flex: 1;
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
}

.chat-input-form button {
  padding: 10px 16px;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.chat-input-form button:disabled {
  background-color: #90caf9;
}
